<template>
  <div class="login-container">
    <div class="container">
      <h1>欢迎光临食尚阁</h1>
      <p class="welcome-message">美味等待着您</p>

      <form @submit.prevent="submitLogin">
        <label for="role">请选择身份：</label>
        <select id="role" v-model="role" required>
          <option value="customer">顾客</option>
          <option value="admin">管理员</option>
          <option value="counter">前台</option>
        </select>

        <label for="username">用户名：</label>
        <input type="text" id="username" v-model="username" required />

        <label for="password">密码：</label>
        <input type="password" id="password" v-model="password" required />

        <div class="button-container">
          <button type="submit">登录</button>
        </div>
      </form>

      <div class="button-container">
        <a href="/register" style="color: black;">没有密码，点击注册</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      role: "customer", // 默认顾客
    };
  },
  methods: {
    async submitLogin() {
      const { username, password, role } = this;

      if (role === "admin") {
        if (username === "admin" && password === "123456") {
          alert("管理员登录成功");
          localStorage.setItem("role", "admin");
          this.$router.push("/admin");
        } else {
          alert("管理员账号或密码错误");
        }
      } else if (role === "counter") {
        if (username === "counter" && password === "123456") {
          alert("前台登录成功");
          localStorage.setItem("role", "counter");
          this.$router.push("/counter");
        } else {
          alert("前台账号或密码错误");
        }
      } else {
        try {
          const response = await fetch("http://localhost:8080/api/login", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ username, password }),
          });

          const result = await response.json();

          if (response.ok) {
            alert("顾客登录成功");
            localStorage.setItem("username", username);
            this.$router.push("/menu");
          } else {
            alert("登录失败：" + result.message);
          }
        } catch (error) {
          alert("登录时发生错误: " + error.message);
        }
      }
    },
  },
};
</script>

<style>
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  background-size: cover;
  background-image: url("./img/background.jpg");
  color: black;
  font-family: Arial, sans-serif;
}

.container {
  text-align: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 36px;
  margin-bottom: 20px;
}

label {
  display: block;
  font-size: 18px;
  margin-top: 20px;
}

input,
select {
  width: 300px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
  box-sizing: border-box;
}

button {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2980b9;
}

p {
  font-size: 24px;
  margin-top: 20px;
}

.login-link {
  color: black;
  font-size: 16px;
  text-decoration: none;
}

.login-link:hover {
  text-decoration: underline;
}
</style>
